#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.gameBackground {
  width: 288px;
  height: 512px;
  overflow: hidden;
  background-image: url(./img/background.png);
  background-position-x: 0px;
  background-size: contain;
  animation: none;
  overflow: hidden;
  position: relative;
}

.gameBackground-start {
  width: 288px;
  height: 512px;
  overflow: hidden;
  background-image: url(./img/background.png);
  background-position-x: 0px;
  background-size: contain;
  animation: bgScroll 3s infinite linear;
  overflow: hidden;
  position: relative;
}

@keyframes bgScroll {
  0% {
    background-position-x: 0px;
  }
  50% {
    background-position-x: -144px;
  }
  100% {
    background-position-x: -288px;
  }
}

.character {
  position: absolute;
  height: 35px;
  width: 52px;
  transition: all 0.2s;
  transform: rotate(0deg);
  animation: characterJumpRotate 0.2s ease-in;
}

@keyframes characterJumpRotate {
  0%{
    transform: rotate(0deg);
  }
  50%{
    transform: rotate(-15deg);
  }
  100%{
    transform: rotate(0deg);
  }
}

.character-drop {
  position: absolute;
  height: 35px;
  width: 52px;
  transform: rotate(0deg);
  transition: all 1s ease-in;
  animation: characterDropRotate 1s ease-in;
}

@keyframes characterDropRotate {
  0%{
    transform: rotate(0deg);
  }
  50%{
    transform: rotate(25deg);
  }
  100%{
    transform: rotate(0deg);
  }
}

.character-reset {
  position: absolute;
  height: 35px;
  width: 52px;
  transform: rotate(0deg);
}

.birdImg {
  width: 100%;
  height: 100%;
  position: absolute;
}

.gameTitle {
  position: absolute;
}

.gameTitle {
  width: 100%;
  text-align: center;
  font-family:cursive;
  font-size: larger;
  color: #fffd9f;
}

.tubeStay {
  position: absolute;
  width: 52px;
  height: 320px;
  transform: translateX(308px);
}

.tubeMove {
  position: absolute;
  width: 52px;
  height: 320px;
  transform: translateX(308px);
  animation: tubeMoving 3s infinite linear;
}

@keyframes tubeMoving {
  0%{
    transform: translateX(308px);
  }
  100%{
    transform: translateX(-52px);
  }
}

.endScreenText {
  width: 100%;
  margin-top: 200px;
  text-align: center;
  font-family:cursive;
  font-size: larger;
  color: #fffd9f;
}